@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600&display=swap);
body{padding-top:25px;margin:0px;font-family:"Raleway", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#282c34;height:100vh;display:flex;flex-direction:row;justify-content:center;align-items:center;color:white}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.footer{padding:10px;width:450px;text-align:center;margin:15px auto 30px auto;font-size:0.8em;color:#fff}.footer a{font-weight:500;text-decoration:none;color:#fff}@media (max-width: 750px){body{padding-top:0px}.footer{padding:5px;margin:5px auto 5px auto}}

.App{z-index:-1;position:fixed;top:0;left:0;width:100%;max-height:100%;text-align:center}.App-logo{max-height:100vh;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 30s linear;animation:App-logo-spin infinite 30s linear}}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@media (max-width: 750px){.App{top:50%;left:50%;transform:translate(-50%, -50%)}}

.container{display:flex;flex-direction:row;justify-content:space-evenly;height:85vh;width:100vw;padding:2vh 2vw}.container .box{display:block;height:100%;box-shadow:0 0 10px #61dafb;border-radius:5px}.container .box .box-title{background-color:#61dafb;color:#282c34;margin:0;padding:1vh 2vh;border-top-left-radius:5px;border-top-right-radius:5px;font-size:2.5vh;font-weight:500}.container .box #editor,.container .box #preview{background-color:#282c34;color:white;opacity:0.95;width:40vw;height:92.5%;overflow:scroll;border-bottom-left-radius:5px;border-bottom-right-radius:5px;padding:5px}.container .box #editor h5,.container .box #preview h5{text-align:justify}.container .box #editor a,.container .box #preview a{color:#61dafb}.container .box #editor blockquote,.container .box #preview blockquote{border-left:3px solid #61dafb;color:#61dafb;padding-left:5px;margin-left:25px}.container .box #editor code,.container .box #preview code{background:#282c34;color:#61dafb;padding:1px 4px 2px 4px;font-size:12px;font-weight:bold}.container .box #editor pre,.container .box #preview pre{background:#282c34;padding:5px 0 5px 5px}.container .box #editor table,.container .box #preview table{border-collapse:collapse}.container .box #editor td,.container .box #editor th,.container .box #preview td,.container .box #preview th{border:2px solid;padding-left:5px;padding-right:5px}@media (max-width: 750px){.container{align-items:center;padding:1vh 1vw;flex-direction:column;height:90vh;justify-content:space-around}.container .box{width:95vw;height:42.5vh}.container .box #editor,.container .box #preview{width:98%;height:85%}}

